
import { defineComponent } from 'vue'

interface FullColumnItemData {
  name: string,
  suffix?: string,
  text: string,
  link?: string,
  time: {
    from: string,
    to: string
  }
}

const FullColumnItem = defineComponent({
  props: {
    itemData: Object as () => FullColumnItemData
  }
})

export default FullColumnItem
