
import { defineComponent } from 'vue'

interface PorfolioRowData {
  image: string,
  title: string,
  description: string,
  tags: {
    [index: number]: string
  },
  links?: {
    project: string,
    demo: string
  }
}

const PorfolioRow = defineComponent({
  props: {
    porfolioIdx: Number,
    porfolioRowData: Object as () => PorfolioRowData,
    focusCallback: Function
  }
})

export default PorfolioRow
